import React, { useContext } from "react";
import { AppContext } from "../App";

function Key({ keyVal, bigKey,correct,wrong, disabled }) {
  const { gameOver, onSelectLetter, onDelete, onEnter } =
    useContext(AppContext);

  const selectLetter = () => {
    if (gameOver.gameOver) return;
    if (keyVal === "ENTER") {
      onEnter();
    } else if (keyVal === "DELETE") {
      onDelete();
    } else {
      onSelectLetter(keyVal);
    }
  };
  if(bigKey)
  {
    return (
      <div
        className="key"
        id="big"
        onClick={selectLetter}
      >
        {keyVal}
      </div>
    );
  }
  else if(disabled)
  {
    return (
      <div
        className="key"
        id="disabled"
        onClick={selectLetter}
      >
        {keyVal}
      </div>
    );
  }
  else if(correct){
    return (
      <div
        className="key"
        id="green"
        onClick={selectLetter}
      >
        {keyVal}
      </div>
    );
  }
  else if(wrong){
    return (
      <div
        className="key"
        id="red"
        onClick={selectLetter}
      >
        {keyVal}
      </div>
    );
  }
  return (
    <div
      className="key"
    //  id={bigKey ? "big" : disabled && "disabled"}
      onClick={selectLetter}
    >
      {keyVal}
    </div>
  );
}

export default Key;
